<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="" prop="name" >
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="storeType">
                <el-select v-model="form.storeType" filterable placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="agencyId" v-if="form.storeType==1" >
                <el-select v-model="form.agencyId" placeholder=""  filterable>
                    <el-option
                        v-for="item in agencyList"
                        :key="item.agencyId"
                        :label="item.agencyName"
                        :value="item.agencyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="companyId" v-if="form.storeType==2" >
                <el-select v-model="form.agencyId" placeholder=""  filterable>
                    <el-option
                        v-for="item in companyList"
                        :key="item.companyId"
                        :label="item.companyName"
                        :value="item.companyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="address">
                <el-input v-model="form.address"></el-input>
            </el-form-item>
            <el-form-item label="--" prop="areaLinkage">
                <region v-model="form.areaLinkage" :startLevel="startLevel" :endLevel="endLevel"></region>
            </el-form-item>
            <el-form-item label="" prop="lonLat" placeholder=",">
                <el-input v-model="form.lonLat"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary"></el-button>
            <el-button @click="resetForm"></el-button>
        </span>
    </el-dialog>
</template>

<script>
import Region from '@/components/common/Region';

export default {
    name:'editorStore',
    props:['currentStoreInfo','title'],
    components:{Region},
    data(){
        return {
            startLevel:2,//
            endLevel:4,//
            dialogVisible:false,
            agencyList:[],
            companyList:[],
            form: {
                address:'',//
                agencyId:'',//Id
                cityId:'',//Id
                areaLinkage:[],//
                companyId:'',//Id
                districtId:'',//Id
                name:'',//
                status:'',
                storeType:'',//1.2.
                storeState:'',//1.2
                townId:'',//Id
                lonLat:''//
            },
            // 
            rules: {
                companyId: [{ required: true, message: '', trigger: 'blur' }],
                agencyId:[{ required: true, message: '', trigger: 'blur' }],
                areaLinkage: [{ required: true, message: '', trigger: 'blur' }],
                address:[{ required: true, message: '', trigger: 'blur' }],
                name:[{ required: true, message: '', trigger: 'blur' }],
                storeType:[{ required: true, message: '', trigger: 'blur' }],
                lonLat:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    created(){
        // ;
        this.agencyList=[
            {
                agencyId:'1',
                agencyName:''
            },
            {
                agencyId:"2",
                agencyName:''
            }
        ];
        this.companyList=[
            {
                companyId:'1',
                companyName:''
            },
            {
                companyId:"2",
                companyName:''
            }
        ]
        // ;
        this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                console.log(data,'success');
            })
            .catch(function(err){
                console.log(err,'err')
            });
        // ;
        this.$http.get(this.$apiUrl.company.list)
            .then(function(data){
                console.log(data,'success');
            })
            .catch(function(err){
                console.log(err,'err')
            })
    },
    methods:{
        open() {
            if(this.title==''){
                this.form=Object.assign({},this.currentStoreInfo,{areaLinkage:[this.currentStoreInfo.cityId,this.currentStoreInfo.districtId,this.currentStoreInfo.townId]});
            }else{
                this.form= {
                    address:'',//
                    agencyId:'',//Id
                    cityId:'',//Id
                    areaLinkage:[],//
                    companyId:'',//Id
                    districtId:'',//Id
                    name:'',//
                    status:'',
                    storeType:'',//1.2.
                    storeState:'',//1.2
                    townId:'',//Id
                    lonLat:''//
                }
            }
            this.dialogVisible = true;
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => { 
                this.$refs.form.resetFields(); 
                done();
            })
            .catch(_ => {});
        },
        submitForm() {
            console.log(this.form,111)
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    if(this.title==''){
                        this.$emit('editSuccess',this.form);
                    }else{
                        if(this.form.areaLinkage.length){
                            this.form.cityId=this.form.areaLinkage[0];
                            this.form.districtId=this.form.areaLinkage[1];
                            this.form.townId=this.form.areaLinkage[2];
                        };
                        let realForm=Object.assign({},this.form);
                        delete realForm.companyList;
                        this.$http.post(this.$apiUrl.store.add,realForm)
                            .then(function(data){
                                console.log(data,'success');
                            })
                            .catch(function(err){
                                console.log(err,'err')
                            });
                        this.$emit('addSuccess',realForm);
                    };
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm() {
            this.$refs.form.resetFields();
            this.dialogVisible=false;
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>

</style>




