<template>
  <el-container>
      <el-main>
          <bread-crumb :items="breadCrumb"></bread-crumb>
          <div class="gap-2">
                <div></div>
                <!--<el-input type="textarea" v-model="remark" rows="10" placeholder=""></el-input>-->
                <div style="padding-left:75px;">{{message}}</div>
                <el-upload
                    class="upload-demo gap-2"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :before-remove="beforeRemove"
                    multiple
                    :limit="3"
                    :on-exceed="handleExceed">
                    <el-button size="small" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">(txt,doc,docx,xls,xlsx,ppt,pptx,pdf,jpg,jpeg,png,gif,rar,zip)</div>
                </el-upload>
            </div>
            <div style="text-align:center;margin-top:40px;">
                <el-button type="primary" @click="back"></el-button>
            </div>
      </el-main>
  </el-container>
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
export default {
    name:'FollowUpDetail',
    components:{BreadCrumb},
    data(){
        return {
            breadCrumb: [{text:''},{text: ""},{text:''},{text:''}],
            message:'',
            id:this.$route.query.id
        }
    },
    created(){
        let self=this;
        this.$http.get(this.$apiUrl.agent.followUp+"?id="+this.id)
            .then(function(data){
                self.message=data.data.data.message;
            })
            .catch(function(err){
                console.log(err);
            })
    },
    methods:{
        back(){
            history.back();
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>

</style>





