<template>
    <el-container>
        <el-main>
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div class="gap-2">
                <div></div>
                <el-input type="textarea" v-model="message" rows="10" placeholder=""></el-input>
                <el-upload
                    class="upload-demo gap-2"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :before-remove="beforeRemove"
                    multiple
                    :limit="3"
                    :on-exceed="handleExceed">
                    <el-button size="small" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">(txt,doc,docx,xls,xlsx,ppt,pptx,pdf,jpg,jpeg,png,gif,rar,zip)</div>
                </el-upload>
            </div>
            <div style="text-align:center;margin-top:40px;">
                <el-button type="primary" @click="submit"></el-button>
            </div>
        </el-main>
    </el-container>
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
export default {
    name:'AddFollowUp',
    components:{BreadCrumb},
    data(){
        return {
            agencyId:this.$route.query.agencyId,
            breadCrumb: [{text:''},{text: ""},{text:''},{text:''}],
            message:'',
            fileKey:'',//key
        }
    },
    methods:{
        submit(){
            let self=this;
            let form={
                agencyId:this.agencyId,
                message:this.message,
                fileKey:this.fileKey
            };
            this.$http.put(this.$apiUrl.agent.followUp,form)
                .then(function(data){
                    self.message="";
                    alert('');
                    self.$router.push({name:"FollowUp",query:{agencyId:self.agencyId}});
                })
                .catch(function(err){
                    console.log(err)
                })
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>

</style>





