<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
        <el-form :model="form" :rules="rules" ref="form" label-width="110px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="name" class="tl">
                        <el-input v-model="form.name" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="abbreviation">
                        <el-input v-model="form.abbreviation" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="cityList" class="tl">
                        <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="deposit">
                        <el-input v-model="form.deposit"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="organizationCode" class="tl">
                        <el-input v-model="form.organizationCode" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item prop="businessType" label="" placeholder="">
                        <el-select v-model="form.businessType" filterable>
                                <el-option label="" :value="3"></el-option>
                                <el-option label="" :value="1"></el-option>
                                <el-option label="" :value="2"></el-option>
                            </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="bd" prop="bdName"  label-width="40px" class="tl">
                <el-input v-model="form.bdName" placeholder=""></el-input>
            </el-form-item>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="cooperationTime">
                            <el-date-picker
                                v-model="form.cooperationTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="corporate" >
                        <el-input v-model="form.corporate"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" class="tl" prop="corporatePhone">
                        <el-input v-model="form.corporatePhone" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="address" >
                        <el-input v-model="form.address"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" class="tl" prop="agencyId">
                <el-select v-model="form.agencyId" placeholder="" @focus="agencyList" filterable>
                    <el-option label="" :value="0"></el-option>
                    <el-option
                        v-for="item in agencyInfoList"
                        :key="item.agencyId"
                        :label="item.agencyCompanyName"
                        :value="item.agencyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item  class="tl upload">
                <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :limit="3"
                :on-exceed="handleExceed">
                    <el-button size="small" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">(txt,doc,docx,xls,xlsx,ppt,pptx,pdf,jpg,jpeg,png,gif,rar,zip)</div>
                </el-upload>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary"></el-button>
            <el-button @click="resetForm"></el-button>
        </span>
    </el-dialog>
</template>

<script>
import Region from '@/components/common/Region'
export default {
    name:'editorCompany',
    props:['companyId','title'],
    components:{Region},
    data(){
        return {
            agencyInfoList:[],
            dialogVisible:false,
            startLevel:1,//
            endLevel:2,//
            form:{
                abbreviation:'',//;
                address:'',//
                agencyId:'',//Id
                bdName:'',//bd
                businessType:'',//,01.2.3.
                cityId:'',//Id
                cityList:[],//number
                corporate:'',//
                corporatePhone:'',//
                cooperationTime:[],//
                deposit:'',//
                comapnyId:'',//Id;
                name:"",//;
                organizationCode:'',//
                provinceId:'',//Id
                resourceKey:'',//key 
            },
            // 
            rules: {
                name: [{ required: true, message: '', trigger: 'blur' }],
                abbreviation: [{ required: true, message: '', trigger: 'blur' }],
                cityList: [{ required: true, message: '', trigger: 'blur' }],
                deposit: [{ required: true, message: '', trigger: 'blur' }],
                organizationCode: [{ required: true, message: '', trigger: 'blur' }],
                cooperationTime: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    created(){
        let self=this;
        //;
        this.$http.get(this.$apiUrl.agent.list)
        .then(function(data){
            self.agencyInfoList=data.data.data;
        })
        .catch(function(err){
            console.log(err,'');
        });
    },
    methods:{
        open() {
            let self=this;
            if(this.title==''){
                // ;
                this.$http.get(this.$apiUrl.company.detail+"?companyId="+this.companyId)
                    .then(function(data){
                        self.form=data.data.data;
                        self.form.cityList=[self.form.provinceId,self.form.cityId];
                        self.form.cooperationTime=[self.form.cooperationStart,self.form.cooperationEnd];
                        console.log(self.form,'');
                })
                .catch(function(err){
                    console.log(err);
                });
            }else{
                this.form={
                    abbreviation:'',//;
                    address:'',//
                    agencyId:'',//Id;
                    bdName:'',//bd
                    businessType:'',//,01.2.3.
                    cityId:'',//Id
                    cityList:[],//number
                    corporate:'',//
                    corporatePhone:'',//
                    cooperationTime:[],//
                    deposit:'',//
                    companyId:'',//id;
                    name:"",//;
                    organizationCode:'',//
                    provinceId:'',//Id
                    resourceKey:'',//key 
                }
            }
            this.dialogVisible = true;
        },
        agencyList(){
            let self=this;
            this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                self.agencyInfoList=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => { 
                this.$refs.form.resetFields(); 
                done();
            })
            .catch(_ => {});
        },
        submitForm() {
             this.$refs.form.validate((valid) => {
               if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    if(this.title==''){
                        this.form.cooperationStart=this.form.cooperationTime[0];
                        this.form.cooperationEnd=this.form.cooperationTime[1];
                        this.$http.post(this.$apiUrl.company.add,realForm)
                            .then(function(data){
                                console.log(data);
                            })
                            .catch(function(error){
                                console.log(error)
                            });
                        this.$emit('editSuccess',this.form);
                    }else{
                        if(this.form.cityList.length){
                            this.form.provinceId=this.form.cityList[0];
                            this.form.cityId=this.form.cityList[1];
                        };
                        this.form.cooperationStart=this.form.cooperationTime[0];
                        this.form.cooperationEnd=this.form.cooperationTime[1];
                        let realForm=Object.assign({},this.form);
                        delete realForm.cityList;
                        this.$http.put(this.$apiUrl.company.add,realForm)
                            .then(function(data){
                                alert(12)
                                console.log(data);
                            })
                            .catch(function(error){
                                console.log(error)
                            });
                        this.$emit('addSuccess',realForm);
                    };
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm() {
            this.$refs.form.resetFields();
            this.dialogVisible=false;
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            // this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>
    .tl .el-form-item__label{
        text-align: left;
    }
    .upload .el-form-item__content{
        margin-left: 0!important;
    }
    .team-time .el-form-item__error{
        width:120px;
    }
</style>




