<template>
    <div>
        <el-form ref="form" :model="form" label-width="180px" class="gap-2">
            <el-row >
                <el-col :span="12">
                    <el-form-item label="" prop="submitName">
                        <el-input v-model="form.submitName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="auditTime">
                            <el-date-picker
                                v-model="form.auditTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="auditName">
                        <el-input v-model="form.auditName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="agencyName">
                        <el-input v-model="form.agencyName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-row :gutter="20">
            <el-col :span="2" :offset="11">
                <el-button class="reset" @click="resetForm"></el-button>
            </el-col>
            <el-col :span="2">
                <el-button type="primary" @click="search"></el-button>
            </el-col>
        </el-row>
        <div class="search-result" > 956</div>
        <el-table :data="searInfoList" border style="width: 100%">
            <el-table-column prop="name" label="" align="center" ></el-table-column>
            <el-table-column prop="typeName" label="" align="center" ></el-table-column>
            <el-table-column prop="submitterName" label="" align="center" ></el-table-column>
            <el-table-column prop="auditorName" label="" align="center"></el-table-column>
            <el-table-column prop="endReason" label="" align="center"></el-table-column>
            <el-table-column prop="endReason" label="" align="center"></el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="form.currentPage"
                :page-sizes="[10, 20, 50, 100,500]"
                :page-size="form.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
export default {
    name:'auditNoPass',
    data(){
        return {
            // 
            total:100,
            // 
            form: {
                auditName:'',//
                auditType:'2',//
                auditTime:[],//
                agencyName:'',//
                currentPage:1,//1
                pageSize:10,//10
                submitName:'',//
                targetState:'2',//
            },
            tableData:[
                {
                    name:'key',
                    submitterName:'wk',
                    auditorName:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                }
            ]
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.form.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.form.currentPage=val;
            this.requestList();
        },
        resetForm() {
            this.$refs.form.resetFields();
        },
        search(){
            this.requestList();
        },
        // 
        requestList(){
            let self=this;
            this.$http.post(this.$apiUrl.agent.firstWaitAuditList,this.form)
                .then(function(data){
                    console.log(data,'');
                })
                .catch(function(err){
                    console.log(err,'');
                });
        }
    },
    computed:{
        //
        searInfoList(){
            return this.tableData.slice((this.form.currentPage-1)*this.form.pageSize,this.form.currentPage*this.form.pageSize); 
        }
    },
}
</script>

<style>

</style>





